.message {
    display: -ms-flexbox;
    display: flex;
}

.message__question {
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.message__answer {
    -ms-flex-pack: start;
        justify-content: flex-start;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.message__content {
    white-space: pre-wrap;
    width: 80%;
    padding-top: 5px;
    padding-right: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    margin-top: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    border: 1px solid rgba(1, 1, 1, 0.2);
    overflow-wrap: break-word;
}

.message__content .message__created-date {
    color: #78909c;
    font-size: 0.75rem;
    font-style: italic;
}

.message__question .message__content {
    background-color: #ffffff;
}

.message__answer .message__content {
    background-color: #eceff1;
}

.message__answer .message__avatar{
    margin-top: 8px;
    margin-right: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
}



