.message-input__text {
    width: 100%;
}

.message-input__icon-button:focus,
.message-input__icon-button:hover,
.message-input__icon-button:hover svg {
    color: #559ded !important;
    background-color: transparent !important;
}

.message-input__icon-button--end {
    margin-right: -8px !important;
}

.message-input__icon-button--disabled {
    pointer-events: inherit !important;
    cursor: not-allowed !important;
}



