.chat {
    width: 100%;
    max-width: 800px;
    height: 100vh;
    margin: auto;
}

.chat .chat__content {
    position: relative;
    height: inherit;
    border-radius: 0px;
    border: 1px solid #b0bec5;
}

.chat .chat__content .chat__header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    color: #fff;
    background-color: #559ded;
    padding: 0, 0, 0, 20px;
}

.chat .chat__content .chat__header-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.chat .chat__content .chat__header-content .chat__title {
    padding: 10px;
}

.chat .chat__content .chat__header-content .chat__avatar {
    margin-right: 4px;
    border: #2c3e50 solid 0.5px;
    border-radius: 50%;
}

.chat .chat__content .chat__body {
    position: absolute;
    top: 70px;
    bottom: 100px;
    width: 100%;
    overflow: auto !important;
}

.chat .chat__content .chat__footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    overflow-y: auto !important;
    background: #fff;
    border-top: 1px solid #b0bec5;
    -ms-flex-direction: column;
        flex-direction: column;
}

.chat .chat__loader {
    width: 30px !important;
    height: 30px !important;
    color: #559ded;
}

.chat .chat__content-footer--link{
    margin: 15px 0 0 0;
}



